import {
  AUTH_BEGIN,
  AUTH_SUCCESS,
  AUTH_FAILURE
} from '../actions/authActions';


const initialState = {
  loggedInUser: [],
  agencies: [],
  loading: false,
  error: null
};

export default function authReducer(state = initialState, action) {
  switch(action.type) {
    case AUTH_BEGIN:
      return {
        ...state,
        loading: true,
        error: null
      };

    case AUTH_SUCCESS:
      return {
        ...state,
        loading: false,
        agencies: action.data.agencies,
        loggedInUser: action.data.loggedInUser
      };

    case AUTH_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.error,
        agencies: [],
        loggedInUser: []
      };

    default:
      return state;
  }
}