import React, { useState } from 'react';

/* UI Elements for Bootstrap 4 */
import { Container, Row, Col, Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

/* react-bootstrap-table-next Data grid code */
import BootstrapTable from 'react-bootstrap-table-next';
import filterFactory, { selectFilter } from 'react-bootstrap-table2-filter';
import paginationFactory from 'react-bootstrap-table2-paginator';
import overlayFactory from 'react-bootstrap-table2-overlay';

import { connect } from "react-redux";
import { fetchRequests, createNewRequest } from "../../actions/requestActions";
import { push } from 'connected-react-router'

const RequestListing = (props) => {

	const {
	  buttonLabel,
	  loadAllRequests,
	  createNewRequest,
	  className,
	  error, 
	  loading, 
	  requests,
	  fetchRequests
	} = props;

	const [modal, setModal] = useState(false);

	const toggle = () => setModal(!modal);

	const closeBtn = <button className="close" onClick={toggle}>&times;</button>;

	const newRequest = () => {
		createNewRequest();
	}

	if (error) {
	  return <div>Error! {error.message}</div>;
	}

	/*if (loading) {
	  return <div>Loading...</div>;
	}*/

	const statusOptions = {
		'': 'All',
		'Active': 'Active',
		'Expired': 'Expired',
		'Terminated': 'Terminated'
	};

	const columns = [{
		dataField: 'copy',
		text: '',
		sort: false,
		formatter: copyFormatter,
		formatExtraData: {}
	},{
		dataField: 'police_number',
		text: 'Police Number',
		sort: true
	}, {
		dataField: 'device_number',
		text: 'Device Number',
		sort: true
	}, {
		dataField: 'service',
		text: 'Service',
		sort: true
	}, {
		dataField: 'start_time',
		text: 'Start Time',
		sort: true
	}, {
		dataField: 'end_time',
		text: 'End Time',
		sort: true
	}, {
		dataField: 'update',
		text: 'Update',
		sort: true,
		align: 'center'
	}, {
		dataField: 'status',
		text: 'Status',
		sort: true,
		style: { 'textAlign': 'centre' }
		/*formatter: cell => statusOptions[cell],
		filter: selectFilter({
			options: statusOptions
		})*/
	},{
		dataField: 'delete',
		text: 'Delete',
		sort: false,
		align: 'center',
		formatter: rankFormatter,
		formatExtraData: {},
		events: {
		    onClick: (e, column, columnIndex, row, rowIndex) => {
		    	row.status == 'Active' ? alert('Sure you want to delete?') : '' 
		    }
	    }
	}];


	const defaultSorted = [{
	  dataField: 'status', // if dataField is not match to any column you defined, it will be ignored.
	  order: 'asc' // desc or asc
	}];

	function copyFormatter(cell, row, rowIndex, formatExtraData) {
	  return (
	  	<p>copy</p>    
	  );
	}

	function rankFormatter(cell, row, rowIndex, formatExtraData) {
	  return (
	  	row.status == 'Active' ? <button className="close" onClick={this.toggle}>&times;</button> : ''    
	  );
	}

	return (
		<>
		<Container>
			<Row>
				<Col><h3>Device Location - Manage Requests</h3></Col>
			</Row>
			<hr/>
			<Row>
			  <Col>
			  	<Button color="primary" onClick={newRequest}>+ New Location Request</Button>
			  </Col>
			</Row>
			<BootstrapTable 
				keyField='police_number' 
				bootstrap4
				data={ requests } 
				columns={ columns } 
				pagination={ paginationFactory() } 
				defaultSorted={ defaultSorted } 
				filter={ filterFactory() } 
				loading={ loading }
				overlay={ overlayFactory({ spinner: true, styles: { overlay: (base) => ({...base, background: 'rgba(0,0,0,0)'}) } }) }
				noDataIndication="There are currently no requests."
			/>
		</Container>

		<Modal isOpen={modal} toggle={toggle} className={className}>
			<ModalHeader toggle={toggle} close={closeBtn}>Ready to create this request?</ModalHeader>
			<ModalBody>
				Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et
				dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip
				ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu
				fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt
				mollit anim id est laborum.
			</ModalBody>
			<ModalFooter>
				<Button color="primary" onClick={toggle}>Do Something</Button>{' '}
				<Button color="secondary" onClick={toggle}>Cancel</Button>
			</ModalFooter>
		</Modal>
		</>
	)
}

const mapStateToProps = state => ({
  requests: state.requests.requests,
  loading: state.requests.loading,
  error: state.requests.error
});

const mapDispatchToProps = dispatch => ({
  createNewRequest: value => dispatch(createNewRequest(value))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RequestListing);
