// authProvider.js
import { MsalAuthProvider, LoginType } from 'react-aad-msal';
 
// Msal Configurations
const config = {
  auth: {
    authority: 'https://login.microsoftonline.com/c13465a8-240d-422b-8709-cbb785982a98',
    //authority: 'https://login.microsoftonline.com/organizations/oauth2/v2.0/authorize',
    clientId: 'c13465a8-240d-422b-8709-cbb785982a98',
    redirectURI: "http://localhost:3000/auth/callback"
  },
  cache: {
    cacheLocation: "localStorage",
    storeAuthStateInCookie: true
  }
};
 
// Authentication Parameters
const authenticationParameters = {
  scopes: [
    'user.read'
  ]
}
 
// Options
const options = {
  loginType: LoginType.Popup,
  tokenRefreshUri: window.location.origin + '/auth.html'
}
 
export const authProvider = new MsalAuthProvider(config, authenticationParameters, options)
