import { call, put, takeEvery, takeLatest } from 'redux-saga/effects'
import api from '../services/api'

function* fetchInitialData(action) {
   try {
      yield put({type: "FETCH_REQUESTS_BEGIN"});
      const response = yield call(fetch, 'https://api.myjson.com/bins/1g7qt0');
      const responseBody = response.json();
      console.log(responseBody)
      yield put({type: "FETCH_REQUESTS_SUCCESS", data: responseBody});
   } catch (e) {
      yield put({type: "FETCH_REQUESTS_FAILURE", error: e.message});
   }
}


/*
  Application init call to load initial requests and agency data
*/
function* mySaga() {
  yield takeLatest("FETCH_REQUESTS", fetchInitialData);
}


/******************************************************************************/
/******************************* WATCHERS *************************************/
/******************************************************************************/


export default mySaga;