import {
  FETCH_REQUESTS_BEGIN,
  FETCH_REQUESTS_SUCCESS,
  FETCH_REQUESTS_FAILURE
} from '../actions/requestActions';

const initialState = {
  loggedInUser: [],
  requests: [],
  loading: false,
  error: null
};

export default function requestsReducer(state = initialState, action) {
  switch(action.type) {
    case FETCH_REQUESTS_BEGIN:
      return {
        ...state,
        loading: true,
        error: null
      };

    case FETCH_REQUESTS_SUCCESS:
      return {
        ...state,
        loading: false,
        requests: action.data.requests,
        agencies: action.data.agencies,
        loggedInUser: action.data.loggedInUser
      };

    case FETCH_REQUESTS_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.error,
        requests: []
      };

    default:
      return state;
  }
}