import { push } from 'connected-react-router'

export const AUTH_BEGIN   = 'AUTH_BEGIN';
export const AUTH_SUCCESS = 'AUTH_SUCCESS';
export const AUTH_FAILURE = 'AUTH_FAILURE';


/* FETCH REQUESTS */

export const authBegin = () => ({
  type: FETCH_REQUESTS_BEGIN
});

export const authSuccess = auth => ({
  type: AUTH_SUCCESS,
  payload: { auth }
});

export const authFailure = error => ({
  type: AUTH_FAILURE,
  payload: { error }
});

export const authReceive = auth => async dispatch => {
  dispatch({ type: AUTH_BEGIN });
  try{
    const response = await fetch('https://api.myjson.com/bins/sa178');
    const data = await response.json();
    dispatch({ type: AUTH_SUCCESS, data });
    dispatch(push('/requests'));    
  } catch (error) {
    dispatch({ type: AUTH_FAILURE, error });
    dispatch(push('/error'));       
  }
}
