import {SET_STEP, UPDATE_FORM, SAVE_FORM, RESET_FORM, RESET_FORM_RESPONSE, FETCH_RESPONSE_SUCCESS} from '../actions/FormActions'

const initialState = {
    requestnumber: 1,
    qid: '',
    policenumber: '',
    mobilenumber: '',
    agency: '',
    requestreason: '',
    locationunit: '',
    locationduration: ''
}

const formReducer = (state=initialState, action) => {
    switch(action.type){
        case SET_STEP:
            return{
                ...state,
                step: action.payload
            }
        case UPDATE_FORM:
            return{
                ...state,
                [action.payload.stateType]: action.payload.value
            }
        case SAVE_FORM:
            return{
                ...state,
                qid: action.payload.form.qid,
                policenumber: action.payload.form.policenumber,
                mobilenumber: action.payload.form.mobilenumber,
                agency: action.payload.form.agency,
                requestreason: action.payload.form.requestreason,
                locationunit: action.payload.form.locationunit,
                locationduration: action.payload.form.locationduration
            }    
        case RESET_FORM:
            return{
                ...initialState,
                applicationNumber: state.applicationNumber
            }
        case RESET_FORM_RESPONSE:
            return{
                ...state,
                response: {}
            }
        case FETCH_RESPONSE_SUCCESS:
           // console.log('-->', action.payload)
            return{
                ...initialState,
                requestNumber: state.requestNumber + 1,
                response: action.payload.id,
            }
        default: return true;    
    }
    return state
}

export default formReducer