import { push } from 'connected-react-router'

export const FETCH_REQUESTS   = 'FETCH_REQUESTS';
export const FETCH_REQUESTS_BEGIN   = 'FETCH_REQUESTS_BEGIN';
export const FETCH_REQUESTS_SUCCESS = 'FETCH_REQUESTS_SUCCESS';
export const FETCH_REQUESTS_FAILURE = 'FETCH_REQUESTS_FAILURE';

export const NEW_REQUEST_BEGIN = 'NEW_REQUEST_BEGIN';
export const NEW_REQUEST_SUCCESS = 'NEW_REQUEST_SUCCESS';
export const NEW_REQUEST_FAILURE = 'NEW_REQUEST_FAILURE';

/* FETCH REQUESTS */

export const fetchRequestsBegin = () => ({
  type: FETCH_REQUESTS_BEGIN
});

export const fetchRequestsSuccess = data => ({
  type: FETCH_REQUESTS_SUCCESS,
  payload: { data }
});

export const fetchRequestsFailure = error => ({
  type: FETCH_REQUESTS_FAILURE,
  payload: { error }
});

