export const fetchRequests = value => async dispatch => {
  dispatch({ type: FETCH_REQUESTS_BEGIN });
  const response = await fetch('https://api.myjson.com/bins/sa178');
  const data = await response.json();
  dispatch({ type: FETCH_REQUESTS_SUCCESS, data });
}

/* CREATE NEW REQUEST */

export const newRequestBegin = () => ({
  type: NEW_REQUEST_BEGIN
});

export const newRequestSuccess = data => ({
  type: NEW_REQUEST_SUCCESS
});

export const newRequestFailure = error => ({
  type: NEW_REQUEST_FAILURE
});

export const createNewRequest = () => dispatch => {
  dispatch(push('/create'));
};